.main-skills-block
    padding-top: 120px
    &__wrapper
        padding: 60px 10px 0 12px
        display: grid
        grid-template-columns: repeat(4, minmax(235px, auto))
        grid-template-rows: repeat(2, minmax(235px, auto))
        grid-gap: 13px
        &__card
            border: 1px solid #A9A9A9
            box-sizing: border-box
            box-shadow: 5px 5px 5px #C0C0C0
            &-icon
              padding-top: 25px
              padding-left: 25px
              img
                width: 35px
                height: 35px
            &-title
              margin-top: 15px
              padding-left: 20px
              font-weight: bold
              font-size: 17px
              line-height: 21px
              color: variables.$title_color  
            &-body
                padding: 15px 20px 25px 20px
                font-weight: 500
                font-size: 15px
                line-height: 18px
                color: variables.$title_color

    
       